img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  font-family: "Bloger-Sans", system-ui, sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
}
.page__body {
  min-height: 100%;
  margin: 0;
  color: #444444;
}
.page-header {
  position: relative;
  display: flex;
  flex-direction: row;
  background: #182044;
  color: #192144;
  padding-top: 18px;
  padding-left: 26px;
  padding-right: 25px;
  padding-bottom: 20px;
  z-index: 8;
}
@media (min-width: 768px) {
  .page-header {
    justify-content: space-between;
    padding: 40px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    justify-content: stretch;
    padding: 46px 60px;
    background-color: #161c35;
  }
}
.page-header__logo {
  height: 15px;
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 166px;
    height: 42px;
    padding-top: 5px;
  }
}
@media (min-width: 1440px) {
  .page-header__logo {
    width: 200px;
    height: 50px;
    padding-top: 1px;
  }
}
.page-header__image {
  width: 96px;
  height: 15px;
}
@media (min-width: 768px) {
  .page-header__image {
    width: 106px;
    height: 16px;
    padding-top: 10px;
  }
}
@media (min-width: 1440px) {
  .page-header__image {
    width: 128px;
    height: 19px;
    padding-top: 12px;
  }
}
.page-header__toggle {
  position: absolute;
  top: 50%;
  right: 26px;
  margin-top: -3.5px;
  z-index: 2;
  display: block;
  width: 25px;
  height: 11px;
  cursor: pointer;
  background-color: initial;
  border: none;
}
@media (min-width: 768px) {
  .page-header__toggle {
    right: 50%;
    margin-right: -13px;
    margin-top: -4.5px;
  }
}
@media (min-width: 1440px) {
  .page-header__toggle {
    left: 300px;
    top: 78px;
    width: 60px;
    height: 2px;
    background-color: #ffffff;
    cursor: initial;
  }
  .page-header__toggle::after,
  .page-header__toggle::before {
    display: none;
  }
}
.page-header__toggle::before,
.page-header__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 25px;
  height: 3px;
  border-radius: 1.5px;
  background-color: #ffffff;
}
.page-header__toggle::after {
  top: 8px;
}
.main-nav {
  display: none;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: 975px;
    margin-left: 145px;
  }
}
.main-menu {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 0 25px;
  min-height: 568px;
  background-color: #ffffff;
  z-index: 8;
}
.main-menu--closed {
  display: none;
}
.main-menu--opened {
  display: block;
}
@media (min-width: 768px) {
  .main-menu {
    padding: 40px;
    min-height: 910px;
  }
}
@media (min-width: 1440px) {
  .main-menu {
    display: none;
  }
}
.main-menu__auth {
  display: none;
}
@media (min-width: 768px) {
  .main-menu__auth {
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    font-family: inherit;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.03em;
    font-weight: 400;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 30px;
    padding-right: 32px;
    color: #161C35;
    text-align: center;
    background-color: #FFD74B;
    border-radius: 25px;
    cursor: pointer;
  }
}
.main-menu__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 26px;
  margin-bottom: 71px;
}
@media (min-width: 768px) {
  .main-menu__wrapper {
    justify-content: flex-start;
    padding-top: 0;
    margin-bottom: 91px;
  }
}
.main-menu__logo {
  height: 15px;
}
@media (min-width: 768px) {
  .main-menu__logo {
    width: 166px;
    height: 42px;
    margin-right: 167px;
    padding-top: 5px;
    text-align: right;
    background-repeat: no-repeat;
  }
}
@media (min-width: 1440px) {
  .main-menu__logo {
    width: 200px;
    height: 50px;
    background-image: url("../img/bg-logo.svg");
    background-repeat: no-repeat;
  }
}
.main-menu__toggle {
  display: block;
  width: 18px;
  height: 18px;
  padding: 0;
  border: none;
  background-color: initial;
  background-image: url("../img/icon-close-menu-mobile.svg");
  background-repeat: no-repeat;
  cursor: pointer;
  margin-right: 3px;
}
@media (min-width: 768px) {
  .main-menu__toggle {
    width: 22px;
    height: 22px;
    background-image: url("../img/icon-close-menu-tablet.svg");
    margin-top: 15px;
    margin-right: 145px;
  }
}
.main-menu__navigation,
.main-menu__user {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
}
.main-menu__user {
  margin-bottom: 41px;
}
@media (min-width: 768px) {
  .main-menu__user {
    padding-left: 57px;
    margin-bottom: 129px;
  }
}
.main-menu__navigation {
  margin-bottom: 54px;
  text-align: center;
}
@media (min-width: 768px) {
  .main-menu__navigation {
    padding-left: 57px;
    margin-bottom: 132px;
  }
}
.main-menu__user-item:first-child {
  margin-bottom: 51px;
}
@media (min-width: 768px) {
  .main-menu__user-item:first-child {
    display: none;
  }
}
.main-menu__link {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-family: inherit;
  font-size: 30px;
  line-height: 35px;
  font-weight: 500;
  color: #192144;
  cursor: pointer;
}
.main-menu__link:hover {
  color: #FFD74B;
}
.main-menu__link:active {
  color: rgba(255, 215, 75, 0.3);
}
@media (min-width: 768px) {
  .main-menu__link {
    text-align: left;
    font-size: 60px;
    line-height: 71px;
    font-weight: 500;
  }
}
.main-menu__link--login {
  font-size: 17px;
  line-height: 30px;
  font-weight: 700;
  background-color: #FFD74B;
  border-radius: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 78px;
  padding-right: 81px;
}
.main-menu__link--login:hover {
  color: #192144;
  background-color: #ffbd30;
}
.main-menu__link--login:active {
  background-color: #ffbd30;
  color: rgba(22, 28, 53, 0.3);
}
.main-menu__link--mail,
.main-menu__link--phone {
  position: relative;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
}
.main-menu__link--mail:hover,
.main-menu__link--phone:hover {
  color: #FF8D30;
}
.main-menu__link--mail:active,
.main-menu__link--phone:active {
  color: rgba(255, 215, 75, 0.3);
}
@media (min-width: 768px) {
  .main-menu__link--mail,
  .main-menu__link--phone {
    font-size: 28px;
    line-height: 28px;
  }
}
.main-menu__elipse {
  display: inline-block;
  box-sizing: border-box;
  width: 45px;
  height: 45px;
  background-color: #121936;
  border-radius: 50%;
  padding-top: 14px;
  padding-left: 13px;
  margin-right: 16px;
  vertical-align: middle;
}
@media (min-width: 768px) {
  .main-menu__elipse {
    width: 68px;
    height: 68px;
    padding-left: 20px;
    padding-top: 20px;
    margin-right: 23px;
    vertical-align: baseline;
  }
}
.main-menu__elipse--mail {
  padding-left: 14px;
  padding-top: 16px;
  margin-right: 11px;
}
@media (min-width: 768px) {
  .main-menu__elipse--mail {
    padding-left: 21px;
    padding-top: 22px;
    margin-right: 14px;
  }
}
@media (min-width: 768px) {
  .main-menu__elipse svg {
    width: 27.2px;
    height: 25.69px;
  }
}
@media (min-width: 768px) {
  .main-menu__elipse--mail svg {
    width: 27.62px;
    height: 18.89px;
  }
}
.main-menu__link--phone:hover .main-menu__elipse svg,
.main-menu__link--mail:hover .main-menu__elipse svg {
  fill: #ffbd30;
}
.main-menu__link--phone:active .main-menu__elipse svg,
.main-menu__link--mail:active .main-menu__elipse svg {
  fill: #ffbd30;
  opacity: 0.3;
}
.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: none;
}
@media (min-width: 1440px) {
