.user-list__link--phone {
  background-color: #FF8D30;
  background-image: url("../img/icon-phone-main-nav-desktop.svg");
}
.user-list__link--mail {
  background-color: #ffbd30;
  background-image: url("../img/icon-mail-main-nav-desktop.svg");
}
.user-list__link--login {
  background-color: #FFD74B;
  padding-top: 16px;
  padding-bottom: 14px;
  padding-left: 29px;
  padding-right: 28px;
